body {
  background-image: url("cat-background.c15ea1c6.jpg");
  background-repeat: no-repeat;
  background-position: initial;
  color: #fafafa;
}

.is-hidden {
  visibility: hidden;
  pointer-events: none;
  display: none;
}

.breed-select {
  width: 300px;
  color: #fafafa;
  background-color: #252525;
  position: absolute;
  top: 7%;
  right: 15%;
}

.ss-content .ss-list .ss-option {
  color: rgba(250, 250, 250, .565);
}

.ss-content .ss-list .ss-option:hover, .ss-content .ss-list .ss-option:focus, .ss-content .ss-list .ss-option.ss-highlighted, .ss-content .ss-list .ss-option:not(.ss-disabled).ss-selected {
  color: #252525;
  background-color: #fafafa;
}

.cat-info {
  max-width: 600px;
  border: 3px solid #fafafa;
  margin: 40px 100px;
  padding: 10px;
  box-shadow: 10px 10px 14px #fafafa;
}

.cat-img {
  max-width: 600px;
  margin: 0 auto;
  display: block;
}

.cat-description span, .cat-temperament span {
  font-style: italic;
  font-weight: 700;
}

.loader {
  width: 120px;
  height: 120px;
  background: linear-gradient(#ff3d00 120px, rgba(0, 0, 0, 0) 0) 0 120px no-repeat;
  border-radius: 50%;
  animation: fillLq .5s linear infinite alternate;
  display: block;
  position: absolute;
  top: 50%;
  left: 30%;
  transform: translate(-50%, -50%);
  box-shadow: inset 0 0 20px -5px rgba(255, 255, 255, .5), inset 0 -40px 40px -20px rgba(255, 255, 255, .5);
}

.loader:before {
  content: "";
  width: 40%;
  height: 25%;
  background: rgba(255, 255, 255, .1);
  border-radius: 50%;
  position: absolute;
  top: 20px;
  left: 10px;
  transform: rotate(-45deg);
}

@keyframes fillLq {
  0%, 10% {
    background-position: 0 120px;
  }

  90%, 100% {
    background-position: 0 0;
  }
}

